import { Component,OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Chart } from 'angular-highcharts';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit {
  title = 'app';
  results = {};
  chart={};
  ultimoMes=0;
  ultimoFollowers={
    numero:0,
    porcentaje:0
  };
  ultimoFollowing={
    numero:0,
    porcentaje:0
  };
  last="";

  constructor(private http: HttpClient) {}

  ngOnInit(): void {
      var last=window.location.href.split("/")
      this.last = last[last.length-1];
      // Make the HTTP request:
      this.http.get('https://semantic-ws.herokuapp.com/json/'+last[last.length-1]).subscribe(data => {
      // Read the result field from the JSON response.
        var nowDate = new Date();
        var previus=0;
        var chartFollowers = data["followers_chart"].map(ele=>{
          var arr = ele.date.split("-");
          arr[2]=arr[2].split("T");
          arr[0]=parseInt(arr[0]);
          arr[1]=parseInt(arr[1])-1;
          arr[2]=parseInt(arr[2][0]);
          return [Date.UTC(arr[0],arr[1],arr[2]),ele.followers]
        })

        this.chart = new Chart({
          chart: {
            type: 'line',
            zoomType: 'x',
            backgroundColor:"transparent"
          },
          title: {
            text: ''
          },
          xAxis: {
            type: 'datetime'
          },
          yAxis: {
            gridLineWidth: 0,
            minorGridLineWidth: 0,
            title: {
                text: ''
            }
          },
          legend: {
            enabled: false
          },
          credits: {
            enabled: false
          },
          exporting:{
            enabled:false
          },
          plotOptions: {
            area: {
                fillColor: {
                    linearGradient: {
                        x1: 0,
                        y1: 0,
                        x2: 0,
                        y2: 1
                    },
                    stops: [
                        [0, Highcharts.getOptions().colors[0]],
                        [1, "rgba(124, 181, 236, 0)"]
                    ]
                },
                marker: {
                    radius: 2
                },
                lineWidth: 1,
                states: {
                    hover: {
                        lineWidth: 1
                    }
                },
                threshold: null
            }
          },
          series: [{
            type: 'area',
            name: 'users',
            data: chartFollowers
          }]
        });
      this.results = data;
      this.ultimoFollowers.numero = data["followed_by_month"][data["followed_by_month"].length-1].max - data["followed_by_month"][data["followed_by_month"].length-2].max;
      this.ultimoFollowing.numero = data["followers_chart"][data["followers_chart"].length-1].following - data["followers_chart"][data["followers_chart"].length-2].following;
      this.ultimoFollowers.porcentaje = this.ultimoFollowers.numero/data["followed_by_month"][data["followed_by_month"].length-1].max;
      this.ultimoFollowing.porcentaje = this.ultimoFollowing.numero/data["followers_chart"][data["followers_chart"].length-1].following;
      this.ultimoFollowers.porcentaje = parseFloat(this.ultimoFollowers.porcentaje.toFixed(4));
      this.ultimoFollowing.porcentaje = parseFloat(this.ultimoFollowing.porcentaje.toFixed(4));
      console.log(data);
    });
  }
}



